/* Ð¡ÐµÐºÑ†Ð¸Ñ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸ */
.gallerySection {
	padding: 0 15%;
}

.galleryImages {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-gap: 30px;
}

.galleryImage {
	width: 100%;
	aspect-ratio: 9/12; /* Ð¸Ð»Ð¸ Ð´Ñ€ÑƒÐ³Ð¾Ðµ ÑÐ¾Ð¾Ñ‚Ð½Ð¾ÑˆÐµÐ½Ð¸Ðµ ÑÑ‚Ð¾Ñ€Ð¾Ð½ */
	object-fit: cover;
	background-color: var(--MainDark10); /* Ñ†Ð²ÐµÑ‚ Ñ„Ð¾Ð½Ð° Ð´Ð¾ Ð·Ð°Ð³Ñ€ÑƒÐ·ÐºÐ¸ */
}

.galleryImages img.successLoad:hover {
	transform: scale(1.01);
}

/* Ð—Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº Ð³Ð°Ð»ÐµÑ€ÐµÐ¸ */
.galleryHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 60px;
}

/* ÐÐ´Ð°Ð¿Ñ‚Ð¸Ð² */
@media (max-width: 1024px) {
	.gallerySection {
		padding: 0 5%;
	}
}

@media (max-width: 768px) {
	.gallerySection {
		padding: 0 2.5%;
	}

	.galleryImages {
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 60px;
	}
}

@media (max-width: 480px) {
	.gallerySection {
		padding: 0 12%;
	}

	.galleryHeader {
		flex-direction: column;
		align-items: flex-start;
		gap: 20px;
	}

	.galleryImages {
		grid-template-columns: 1fr;
		grid-gap: 20px;
	}
}

@media (max-width: 320px) {
	.gallerySection {
		padding: 0 6%;
	}
}
