.inputRegistration {
	width: 100%;
}

.registrationForm {
	display: flex;
	flex-direction: column;
	gap: 25px;
	align-items: flex-start;
}

.registrationButton {
	padding: 16px 24px;
}

/* Ð¡Ñ‚Ð¸Ð»Ð¸ Ð±Ð»Ð¾ÐºÐ° Ñ Ð»Ð¾Ð³Ð¸Ð½Ð¾Ð¼ */
.loginBlock {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 15px;
}

.loginBlockText {
	display: flex;
	align-items: center;
	gap: 10px;
}

.loginBlockText p {
	font-family: 'MulishMedium', sans-serif;
	font-size: 1.6rem;
	color: var(--MainDark);
}

.loginBlockText *:last-child {
	font-family: 'MulishBold', sans-serif;
	font-size: 1.6rem;
	color: var(--MainDarkBlue);
	text-decoration: underline;
}

